import fetchJsonp from 'fetch-jsonp';
import { isValidZip, showAlert } from './validate';


const petForm = document.querySelector('#pet-form');

petForm.addEventListener('submit', fetchAnimals);

// Fetch Animal List from API
function fetchAnimals(e) {
  e.preventDefault();

  // Get user input
  const animal = document.querySelector('#animal').value;
  const zip = document.querySelector('#zip').value;

  // Validate Zip
  if(!isValidZip(zip)){
    showAlert('plese enter a valid zip code', 'danger');
    return;
  }

  // Fetch pets
  fetchJsonp(`http://api.petfinder.com/pet.find?format=json&key=e2d4d86f5cbdc1aecf4a54c3bda01144&animal=${animal}&location=${zip}&callback=callback`, {
      jsonpCallbackFunction: 'callback'
  })
    .then(res => res.json())
    .then(data => showAnimals(data.petfinder.pets.pet))
    .catch(err => console.log(err))
}

// Show listing of pets
// The argument 'pets' comes from the showAnimals function argudement in the .then()
function showAnimals(pets) {
  const results = document.querySelector('#results');
  // Clear first
  results.innerHTML = '';
  // Loop through pets array
  pets.forEach((pet) => {
    console.log(pet)
    const div = document.createElement('div');
    div.classList.add('card', 'card-body', 'mb-3');
    div.innerHTML = `
      <div class="row">
        <div class="col-sm-6">
          <h4>${pet.name.$t} (${pet.age.$t})</h4>
          <p class="text-secondary">${pet.breeds.breed.$t}</p>
          <p>${pet.contact.address1.$t} ${pet.contact.city.$t} ${pet.contact.state.$t} ${pet.contact.zip.$t}</p>
          <p class="text-secondary">Phone: ${pet.contact.phone.$t}</p>
            ${pet.contact.email.$t ? `<p class="text-secondary">Email: ${pet.contact.email.$t}</p>` : ``}
            <p class="text-secondary">Shelter ID: ${pet.shelterId.$t}</p>
        </div>
        <div class="col-sm-6 text-center">
          <img class="img-fluid mt-2" src="${pet.media.photos.photo[3].$t}" />
        </div>
      </div>
    `;
    results.appendChild(div);
  })
}




window.addEventListener('scroll', (e) => {
  const header = document.querySelector('header');
  const distanceY = window.pageYOffset;
  const collapsePoint = 300;
  console.log('scrolling...');
  if(distanceY > collapsePoint) {
    header.classList.remove('sticky-nav--visible')
  } else {
    header.classList.add('sticky-nav--visible')
  }
})
